@import "./mixins";

.app {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    height: calc(var(--vh, 1vh) * 100);
    scroll-behavior: smooth;

    @include for-desktop-up {
        flex-direction: row; } }

.header {
    display: flex;
    justify-content: flex-end; }

.content {
    width: 100%;
    scroll-behavior: smooth; }
